.recursosInput {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.bigInput {
    width: 90%;
}

.visorPdfFrame {
    margin: 10px 0px;
    text-align : center;
    position   : relative;
    overflow   : hidden;
    padding-top: 50%;
}

.visorPdf {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
}


