.inicioColumns {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

@media only screen and (max-width: 600px) {
    .inicioColumns{
      grid-template-columns: 1fr;
    }
  }

.inicioText {
    font-size: larger;
}

.inicioNum {
    color: var(--custom-blue);
}

.inicioUltimosList {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}


