.registrarseSetMiddle {
    height: 60vh;
    display: grid;
    -ms-flex-align: center;
        align-items: center;
    place-items: center;
}

.registrarseCard {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background-color: white;
    -webkit-box-shadow: 0px 8px 15px 1px rgba(0,0,0,0.05);
            box-shadow: 0px 8px 15px 1px rgba(0,0,0,0.05);
    border: 1px solid lightgray;
    padding: 0.5rem;
}

.registrarseForm {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.registrarseHeader {
    text-align: center;
}

.registrarseError {
    text-align: center;
    font-size: small;
    color: var(--custom-red);
}


