* {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

:root {
  --custom-blue: #4285f4;
  --custom-red: #ea4335;
}

body {
  background-color: whitesmoke;
}

main {
  margin-left: 10rem;
  padding: 5px;
}

.input {
  margin: 0.5rem;
  padding: 0.5rem;
}

.button {
  margin: 0.5rem;
  padding: 0.5rem;
  border: none;
  color: white;
  background-color: var(--custom-blue);
  border-radius: 0.2rem;
}

.button:hover {
  -webkit-filter: brightness(0.9);
          filter: brightness(0.9);
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  main {
      margin-left: 0;
  }
}

.links {
  font-size: larger;
  text-decoration: none;
  color: var(--custom-blue);
}

.linkBtnBlanco {
  text-decoration: none;
  background-color: white;
  color: black;
  border: 1px solid lightgray;
  -webkit-box-shadow: 0px 8px 15px 1px rgba(0,0,0,0.05);
          box-shadow: 0px 8px 15px 1px rgba(0,0,0,0.05);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0.25rem;
  font-size: small;
}

.linkListContainer{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

@media only screen and (max-width: 600px) {
  .linkListContainer{
    grid-template-columns: 1fr;
  }
}

.linkListTarjeta {
  background-color: white;
  -webkit-box-shadow: 0px 8px 15px 1px rgba(0,0,0,0.05);
          box-shadow: 0px 8px 15px 1px rgba(0,0,0,0.05);
  border: 1px solid lightgray;
  border-radius: 5px;
  margin: 0.5rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  padding: 2rem;
}

.linkListIcon {
  padding-right: 2rem;
}

.linkListLink {
  text-decoration: none;
  color: black;
}

.linkListLink:hover {
  -webkit-filter: brightness(0.9);
          filter: brightness(0.9);
}


